body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form-sample-options {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 15px 0;
}

.center-half {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin: 0.2em 0;
}

.half-left {
  position: absolute;
  right: 50%;
  margin-right: 0.5em;
}

.half-right {
  position: relative;
  left: 50%;
  width: 100px;
  margin-left: 1em;
  /* background: red; */
}

.btn-submit {
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  width: 100px;
  margin: 10px;
}


.output {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 700px;
  margin: 40px auto;
}

fieldset {
  border: none;
}

.btn-back {
  width: 100px;
  margin-left: 1em;
}

.btn-calculate {
  margin: 1em;
}

table {
  margin: 0 auto;
  margin-bottom: 10px;
  background: hsl(0, 0%, 90%);
  padding: 1em;
  border-radius: 1em;
}

.data-sample-id {
  width: 80px;
}

.data-input {
  width: 160px;
}

.App {
  text-align: center;
  margin-bottom: 50px;
  padding-bottom: 100px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.intro {
  text-align: left;
  padding: 1px 1em;
  max-width: 700px;
  margin: 0 auto;
  background: hsl(0, 0%, 93%);
}



